@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*mdl styles*/

header.mdl-layout__header.mdl-layout__header--scroll.header-color{
  background: whitesmoke;
  background-size: cover;
}

div.mdl-layout__content{
  background: whitesmoke;
  background-size: cover;
}

.mdl-layout__header-row a.mdl-navigation__link{
  color: black;
}




/*landing page*/

.landing-grid {
  background: whitesmoke;
  background-size: cover;
  text-align: center;
  position: relative;
  top: 200px;
  left: 0;
  width: 100%;
  height: 100%;
}

.logo-img {
  height: 60px;
  width: 115px;
  margin: 0 auto;
  padding: 10px;
}

/*Landing Banner*/
.banner-text {
  background-color: transparent;
  width: 75%;
  margin: 0 auto;
  padding: 30px;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 50px;
  font-weight: 200;
  color: black;
  font-family: 'Roboto Condensed', sans-serif;
}

.banner-text hr {
  border-top: 2px solid black;
  width: 75%;
  margin: auto;
}

.banner-text p {
  color: black;
  font-size: 20px;
  padding: 1em;
  font-family: 'Cinzel', serif;
}


.main-footer{margin:auto;width:100%;background-color:#1e1f1f;color:white;padding-top:20px;width:100%}.main-footer .address{list-style:none;margin:auto;width:50%}.main-footer .row{margin:auto}.main-footer .row h4{text-align:center}.main-footer .row h5{text-align:center}.main-footer .row .col-sm{margin:auto;width:50%;text-align:center}.main-footer .col .company{margin-left:2em}.main-footer .col p{text-align:center}.main-footer .col input{margin-right:5px;width:250px;margin-left:4.5em}.main-footer .col .submit{padding:3px;border:none}.main-footer .col .socials{list-style:none}.main-footer .col .socials li{display:inline;padding:20px;padding-top:10px}

div.jumbotron{display:flex;flex-direction:row;margin-bottom:0 !important;padding:0;background-color:#5ba3f7;height:580px}div.jumbotron .intro{text-align:center;margin:auto;width:100%}div.jumbotron .intro h2{font-size:75px}div.jumbotron .intro h5{font-size:20px}div.jumbotron .intro button{font-size:20px;border:none;padding:15px;margin:20px;width:200px;height:70px;text-align:center;color:white;background-color:black}

html,body{height:100vh;padding:0;margin:0}.banner-text{margin-bottom:5em}.grow-container{margin:auto;width:100%;height:200px}.grow-container .grow-title{margin:auto;text-align:center;margin-top:3em}.grow-container .signup{display:flex;flex-direction:row;justify-content:center;margin:auto;padding:2em}.grow-container .signup .consult{border:none;padding:20px;height:45px;margin:auto;text-align:center}.grow-container .signup p{padding:35px;margin-top:3em}.grow-container .signup .call{border:none;padding:20px;height:45px;margin:auto;text-align:center}.header-bar{display:block;width:100%;height:150px;padding:0;margin:auto;text-align:center;background:#5ba3f7;color:white}.header-bar ul li{display:inline-block;list-style:none;padding:4em;margin-top:.5em}.main-content .mission-statement{display:inline-block;background:white;width:100%;padding:35px}.main-content .mission-statement .stock{width:40%;height:325px;margin-top:25px;border-right:1px solid rgba(91,163,247,0.3);border-bottom:1px solid rgba(91,163,247,0.3);padding:0 20px 20px 0;margin-bottom:5px}.main-content .mission-statement .statement{width:50%;float:right;margin-right:35px}.main-content .mission-statement .statement span{display:block;text-align:center;font-size:80px}.main-content .mission-statement .statement h5{text-align:center}.main-content .mission-statement .statement p{margin-top:35px}.main-content .capabilities{display:inline-block;background:url(/static/media/capabilities.8acf7094.gif);opacity:0.9;background-position:0 0;background-repeat:no-repeat;background-size:cover;width:100%;height:900px;padding:35px;color:white}.main-content .capabilities span{display:block;text-align:center;font-size:40px}.main-content .capabilities ul{-moz-column-count:2;-moz-column-gap:20px;-webkit-column-count:2;-webkit-column-gap:20px;column-count:2;grid-column-gap:20px;column-gap:20px;padding-bottom:45px;margin-bottom:50px;height:auto}.main-content .capabilities ul li{display:block;list-style:none;padding:4em;margin-top:.5em}.main-content .capabilities ul li img{height:80px;width:145px}.main-content .capabilities ul li a{text-decoration:none;color:white}.about-short{padding:35px;border:1px solid black;margin:20px}.portfolio{padding:35px;display:inline-block}.portfolio .client-1{display:inline-block;width:100%}.portfolio .client-1 img{width:30%;padding:20px;height:auto}.portfolio .client-1 .client-1-info{width:50%;float:right;background:white;padding:20px;height:auto}Footer{position:absolute;left:0;right:0;bottom:0;width:100%}

.about{margin:35px;padding:35px}

.main{display:flex;flex-direction:column;align-items:center;margin:40px auto;max-width:700px;height:250px;background-color:white;border:1px solid black;padding:40px}.main p{text-align:center;margin-bottom:20px;color:#45454d}.main .circles{display:flex}.main .circles span{height:20px;width:20px;margin:0 3px;display:flex;align-items:center;justify-content:center;cursor:pointer}.main .circles span::before{content:"";height:6px;width:6px;background-color:#d4d4d4;border-radius:50%;transition:background-color 0.3s ease}.main .circles span:hover::before{background-color:#45454d}.main .circles span[data-quote="${active}"]::before{background-color:#151516}

body{padding:0;margin:0;width:100%}.main-content .client-content{padding:35px}.main-content .testimonials{padding-top:35px}.main-content .testimonials h4{text-align:center;font-size:35px}

.main-content .body-content{padding:35px}

.contact-container{padding:35px;height:800px;margin:auto;width:50%;text-align:center}.contact-container .contact{display:block;margin:auto;padding:25px}

.logo{overflow:hidden}.logo a{text-decoration:none;color:black}

html,body{overflow:hidden}div.mdl-layout__header-row{background:#5ba3f7}span.logo{display:block;position:absolute;font-family:'Inconsolata', monospace;font-size:25px;float:left;color:black;margin-right:35px}

